.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
nav{
  display : flex;
  justify-content: space-around;
  align-items: center;
  background-color: #fa1977;

}
.navLinks{
  width: 80%;
  font-style: oblique;
  font-size: 95%;
  display : flex;
  list-style: none;
  justify-content: space-around;
  align-items: center;

}
li:hover{
display: block;

cursor: pointer;
}